@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* OVERALL STYLES */
/* FONT STYLES */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  text-transform: uppercase; }

h1 {
  font-size: 2rem; }

h2 {
  font-size: 1.5rem;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  color: #00abdf; }

h3 {
  font-size: 1.4rem; }

h4 {
  font-size: 1rem; }

h5 {
  font-size: .83rem; }

h6 {
  font-size: .67rem; }

p {
  font-family: 'Lato', sans-serif; }

/* END OVERALL STYLES */
/* NAVBAR */
.navBar {
  background-color: #ffffff;
  border-bottom: 1px solid #000000;
  padding: 10px; }
  .navBar img {
    width: auto;
    height: 50px;
    margin-right: 10px; }
  .navBar .navbar-nav a:link, .navBar .navbar-nav a:visited {
    font-family: 'Montserrat', sans-serif;
    color: #000000;
    text-transform: uppercase;
    font-weight: bold; }

/* END NAVBAR */
/* SEPERATOR */
.seperator {
  height: 57px;
  padding: 10px;
  background: #ffffff;
  border-bottom: 1px solid #000000; }

/* END SEPERATOR */
/* SLIDER WRAP */
.sliderWrap {
  background-color: #000; }
  .sliderWrap .sliderInner {
    background-image: url(/static/media/chronicink-slider2.2ea3553d.jpg);
    background-position: center;
    background-repeat: no-repeat;
    height: 600px;
    margin: 0 auto;
    display: flex; }
    .sliderWrap .sliderInner h1 {
      align-self: flex-end;
      text-shadow: 2px 2px 3px #000000;
      color: #00abdf;
      margin-left: 4.5rem; }
  .sliderWrap img {
    display: block;
    margin: 0 auto; }

/* END SLIDER */
/* ABOUT */
.aboutWrap {
  border-top: 1px solid #00abdf;
  background-color: rgba(255, 255, 255, 0.4);
  padding-bottom: 1rem;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: repeat; }
  .aboutWrap div h2 {
    margin-top: 3rem;
    color: #00abdf; }
  .aboutWrap div p {
    font-size: 20px;
    margin-bottom: 2rem; }
  .aboutWrap div .aboutImg {
    float: right;
    shape-outside: polygon(0% 0%, 100% 0%, 100% 100%);
    shape-image-threshold: 500; }

/* END ABOUT */
/* SKILLS */
.skillsWrap {
  border-top: 1px solid #00abdf;
  background-color: rgba(255, 255, 255, 0.4);
  padding-top: 1rem;
  padding-bottom: 2rem;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: repeat; }
  .skillsWrap h2 {
    margin-bottom: 2rem; }
  .skillsWrap .titleWrap {
    margin: 1rem auto 0px auto; }
  .skillsWrap .skillWrapInner {
    margin: 10px auto 0px auto; }
    .skillsWrap .skillWrapInner div h3 {
      color: #00abdf;
      font-size: 1.5rem; }
    .skillsWrap .skillWrapInner div ul {
      list-style: none;
      padding-left: 0px; }
      .skillsWrap .skillWrapInner div ul li p {
        font-size: 1.2rem;
        margin-bottom: 5px;
        font-weight: 700; }

/* END SKILLS */
/* PROJECTS WRAP */
.projectsWrap {
  border-top: 1px solid #00abdf;
  background-color: rgba(255, 255, 255, 0.4);
  padding-top: 1rem;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: repeat; }
  .projectsWrap .cardWrap {
    margin-top: 10px; }
    .projectsWrap .cardWrap .cardWrapInner {
      padding: 10px;
      background-color: #ffffff;
      border: 1px solid #00abdf;
      border-radius: 5px;
      min-height: 400px; }
      .projectsWrap .cardWrap .cardWrapInner .imgWrap {
        overflow: hidden;
        max-height: 200px;
        margin-bottom: 1rem; }
        .projectsWrap .cardWrap .cardWrapInner .imgWrap img {
          width: 100%;
          min-height: 200px; }

/* END PROJECTS WRAP */
/* RESUME WRAP */
.resumeWrap {
  background-color: #ffffff; }
  .resumeWrap .controlWrap {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff; }
    .resumeWrap .controlWrap ul {
      padding-left: 0px;
      list-style: none; }
      .resumeWrap .controlWrap ul li {
        margin-right: 5px; }
  .resumeWrap .noGutters {
    padding-left: 0px;
    padding-right: 0px; }
  .resumeWrap h2 {
    font-size: 2rem;
    color: #00abdf;
    font-family: 'Roboto Condensed', sans-serif;
    margin-bottom: 20px; }
  .resumeWrap .hexWrap {
    display: flex;
    align-items: center;
    justify-content: center; }
  .resumeWrap .headerWrap {
    background-image: url(data:image/jpeg;base64,);
    border-bottom: 5px solid #00abdf;
    padding-bottom: 20px; }
    .resumeWrap .headerWrap h1 {
      color: #00abdf;
      border-bottom: 1px solid #00abdf;
      display: block;
      width: 250px;
      margin-bottom: 0px;
      font-family: 'Roboto Condensed', sans-serif;
      font-size: 2.4rem; }
    .resumeWrap .headerWrap h2 {
      margin: 5px 0px;
      font-size: 1.3rem;
      color: #000000;
      border-bottom: 1px solid #00abdf;
      display: block;
      padding-bottom: 5px;
      width: 250px;
      font-family: 'Roboto Condensed', sans-serif; }
    .resumeWrap .headerWrap p {
      font-weight: 700;
      margin-bottom: 0px;
      text-transform: uppercase; }
      .resumeWrap .headerWrap p a:link, .resumeWrap .headerWrap p a:visited {
        color: #00abdf; }
  .resumeWrap .workplaceWrap h3 {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.5rem;
    color: #00abdf; }
  .resumeWrap .workplaceWrap h4 {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.2rem;
    color: #000; }
  .resumeWrap .workplaceWrap .dateWrap {
    display: flex;
    align-content: flex-end;
    justify-content: flex-end; }
    .resumeWrap .workplaceWrap .dateWrap p {
      font-family: 'Roboto Condensed', sans-serif;
      text-align: right;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 1.2rem;
      align-self: flex-end;
      margin-bottom: 0px; }
  .resumeWrap .workplaceWrap .keywords {
    font-size: .8rem;
    font-style: italic; }
    .resumeWrap .workplaceWrap .keywords span {
      font-weight: 700; }
  .resumeWrap .skillsWrap {
    background: none;
    border-top: none; }
    .resumeWrap .skillsWrap div h2 {
      margin-top: 0px; }
    .resumeWrap .skillsWrap div h3 {
      font-size: 1.2rem; }
    .resumeWrap .skillsWrap div ul li p {
      font-size: 1rem; }

/* END RESUME */
/* COMMON CLASSES */
@media print {
  #Header, #Footer {
    display: none !important; }
  .controlWrap {
    display: none; } }

.shadow {
  box-shadow: 1px 2px 10px -1px rgba(0, 0, 0, 0.75); }

/* END COMMON CLASSES */

